.hp-container {
  .carousel__container {
    max-width: 100vw;

    .slick-dots li button::before {
      color: chocolate;
      font-size: 10px;
    }
  }
}

.service__card {
  text-align: center;
  transition: all 0.3s ease 0s;
  position: relative;
  z-index: 0;

  h3 {
    color: chocolate;
    text-shadow: 1px 1px 1px black;
    font-size: 2rem;
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: 5%;
  }

  &:hover {
    color: chocolate;
  }
}

.price__card {
  margin: 2rem;
  background: rgb(85, 85, 85);
  border-radius: 5px;
  display: flex;
  flex-direction: column;

  .price__card__1 {
    background-color: chocolate;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    font-style: italic;
    text-align: center;
    span {
      font-weight: 900;
    }
  }

  .price__card__2 {
    display: flex;
    justify-content: center;
    font-style: italic;
    border-bottom: 1px solid grey;

    h1,
    h2 {
      color: chocolate;
    }

    h2 {
      margin-bottom: 0;
    }

    h3 {
      font-size: 1.5rem;
      line-height: 0;
      margin-top: 0;
      margin-left: 10px;
    }
  }
  .price__card__info {
    border-bottom: 1px solid grey;
    text-align: center;
    opacity: 0.7;
  }

  .price__card__button {
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;

    button {
      background-color: rgba(210, 105, 30, 1);
      border: none;
      color: white;
      padding: 1rem 4rem;
      border-radius: 2px;
      font-weight: bolder;

      &:hover {
        transition: 0.3s all ease 0s;
        background-color: rgba(210, 105, 30, 0.7);
      }
    }
  }
}
