header {
  display: flex;
  flex-direction: column;

  nav {
    display: none;
  }

  .social__media {
    align-items: center;
    display: flex;
    justify-content: space-around;
    padding: 2rem;

    span {
      font-size: 0.8rem;
    }

    button {
      border: none;
      border-radius: 50px;
      padding: 0.5rem 1rem;
      background-color: rgba(210, 105, 30, 1);
      color: white;
      cursor: pointer;

      &:hover {
        transition: all 0.3s ease 0s;
        background-color: rgba(210, 105, 30, 0.7);
      }
    }

    a {
      &:hover {
        transition: all 0.3s ease 0s;
        color: chocolate;
      }
    }
  }

  .burger {
    display: flex;
    justify-content: center;
    padding: 1rem 0;
    align-items: center;

    &:hover {
      .burger__icon {
        transition: all 0.3s ease 0s;
        color: chocolate;
      }
    }
  }
}

@media screen and (max-width: 550px) {
  nav {
    display: none;
  }
}
